  box-sizing: border-box;
  padding: 15px;
  height: auto;
}

.column {
  flex-basis: 100%;
}

@media screen and (min-width: 800px) {
  .column {
    flex: 1 1;
  }

  ._45 {
  flex: 4.5 1;
}
._40 {
flex: 4.0 1;
}
  ._35 {
  flex: 3.5 1;
}
  ._25 {
  flex: 2.5 1;
}

._30 {
flex: 3.0 1;
}
._50 {
  flex: 5 1;
}
._75 {
  flex: 7.5 1;
}
._55 {
  flex: 5.5 1;
}
._20 {
  flex: 2 1;
}
._34 {
  flex: 3.4 1;
}
._66 {
  flex: 6.6 1;
}
}

@media only screen and (max-width: 800px) {


.linkheader{
  margin-top: 15px !important;
  padding-right: 15px !important;

}
 .linkheader a{
   margin: 0px 8px;
 }

 .mobile-m-0{
